.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100vh; */
  /* min-height: 100vh; */
  height: 100vh;
  background: url("../../public/assets/background-best.jpg") no-repeat center center / cover;
  background-size: cover;
  background-position: center;
  padding: 20px;

  /* animation: changeBackground 25s infinite linear; */
  /* animation-delay: 10s;   Wait 5 seconds before starting */
  /* transition: background-image 3s cubic-bezier(0.19, 1, 0.22, 1); */
}



/*  Signup Card */
.auth-card {
  background: rgba(255, 255, 255, 0.75); /* White with slight transparency */
  padding: 15px; /*  Increased right padding */
  width: 100%; 
  max-width: 450px;
  border-radius: 15px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/*  Signup Heading */
.auth-card h2 {
  margin-bottom: 5px;
  font-size: 22px;
  color: #333;
}

/*  Form Group */
.form-group {
  margin-bottom: 12px;
  text-align: left;
  /* padding-right: 25px; 
  padding-left: 10px; */
}

/*  Form Input Fields */
.form-group input {
  width: 95%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  color: #333;
  /* padding-right: 15px; 🔥 Increased right padding inside input */
}

.form-group input::placeholder {
  color: rgba(0, 0, 0, 0.3);
  font-size: 13px;
}

/*  Error Message */
.error-text {
  color: red;
  font-size: 13px;
  margin-top: 5px;
}

/*  Signup Button */
button {
  width: 99.5%;
  padding: 9.5px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s ease;
  align-items: center;
}

button:hover {
  background: #0056b3;
}

/*  Already have an account? */
.auth-card p {
  font-size: 14px;
  color: #555;
  margin-top: 10px;
}

.auth-card a {
  color: #007bff;
  text-decoration: none;
}

.auth-card a:hover {
  text-decoration: underline;
}

/*  Logo Styling */
.spiway-logo {
  width: 80px;
  margin: 0 auto;
  display: block;
  border-radius:50%;
}

/*  Welcome Text */
.welcome-text {
  font-size: 20px;
  font-weight: bold;
  color: #333;
  margin-top: 10px;
}

/*  Subtext */
.welcome-subtext {
  font-size: 14px;
  color: #666;
  margin-bottom: 15px;
}

/* 🔹 Footer */
.auth-footer {
  margin-top: 15px;
  font-size: 12px;
  color: #777;
  text-align: center;
}

.auth-footer a {
  color: #007bff;
  text-decoration: none;
}

.auth-footer a:hover {
  text-decoration: underline;
}

/* 🔹 Responsive Design */
@media (max-width: 768px) {
  .auth-card {
    padding: 25px 40px 25px 25px; /* 🔥 Adjusted right padding for smaller screens */
    width: 90%;
  }
}

@media (max-width: 480px) {
  .auth-card {
    padding: 20px 30px 20px 20px; /* 🔥 Adjusted for mobile */
    width: 100%;
  }

  .auth-card h2 {
    font-size: 20px;
  }

  .form-group input {
    padding: 8px;
    font-size: 13px;
  }

  button {
    padding: 8px;
    font-size: 14px;
  }

  .auth-footer {
    font-size: 11px;
  }
}

/* 🔹 Extra Small Screens (250px - 479px) */
@media (max-width: 479px) {
  .auth-card {
    padding: 15px 25px 15px 15px; /* 🔥 Adjusted for very small screens */
    width: 95%;
  }

  .auth-card h2 {
    font-size: 18px;
  }

  .form-group input {
    padding: 6px;
    font-size: 12px;
  }

  button {
    padding: 6px;
    font-size: 13px;
  }

  .auth-footer {
    font-size: 10px;
  }
}

/* 🔹 Ultra Small Screens (250px - 320px) */
@media (max-width: 320px) {
  .auth-card {
    padding: 12px 20px 12px 12px; /* 🔥 Adjusted further for tiny screens */
    width: 100%;
  }

  .auth-card h2 {
    font-size: 16px;
  }

  .form-group input {
    padding: 5px;
    font-size: 11px;
  }

  button {
    padding: 5px;
    font-size: 12px;
  }

  .auth-footer {
    font-size: 9px;
  }
}

/* 🔹 Medium-Small Screens (480px - 600px) */
@media (min-width: 480px) and (max-width: 600px) {
  .auth-card {
    padding: 22px 35px 22px 22px; /* 🔥 Adjusted padding */
    width: 85%;
  }

  .auth-card h2 {
    font-size: 21px;
  }

  .form-group input {
    padding: 9px;
    font-size: 14px;
  }

  button {
    padding: 9px;
    font-size: 15px;
  }

  .auth-footer {
    font-size: 12px;
  }
}


